@import "https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: poppins, sans-serif;
}

body {
  min-height: 100vh;
  background-image: url("https://img.goodfon.ru/original/1920x1080/4/a7/cherry-blossom-tree-on-cliff-digital-wallpaper-cherry-blosso.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  display: flex;
}

section {
  min-width: 600px;
  -webkit-backdrop-filter: blur(55px);
  backdrop-filter: blur(55px);
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(255, 255, 255, .5);
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 2rem 3rem;
  display: flex;
  position: relative;
}

.go-back {
  position: absolute;
  top: 10px;
  left: 15px;
}

.form {
  width: 100%;
}

h1 {
  color: #fff;
  text-align: center;
  font-size: 2rem;
}

.inputbox {
  max-width: 500px;
  border-bottom: 2px solid #fff;
  margin: 30px 0;
  position: relative;
}

.icon {
  fill: #fff;
  position: absolute;
  bottom: 5px;
  right: 2px;
}

.inputbox label {
  color: #fff;
  pointer-events: none;
  font-size: 1rem;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

input:focus ~ label, input:valid ~ label {
  top: -5px;
}

.inputbox input {
  width: 100%;
  height: 60px;
  color: #fff;
  background: none;
  border: none;
  outline: none;
  padding: 0 35px 0 5px;
  font-size: 1rem;
}

.inputbox ion-icon {
  color: #fff;
  font-size: 1.2rem;
  position: absolute;
  top: 20px;
  right: 8px;
}

.button-wrapper {
  gap: 10px;
  display: flex;
}

button {
  width: 50%;
  height: 40px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 40px;
  outline: none;
  font-size: 1rem;
  font-weight: 600;
  transition: all .4s;
}

button:hover {
  background-color: rgba(255, 255, 255, .5);
}

input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .5s linear, visibility .5s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.modal {
  min-width: 600px;
  min-height: 600px;
  background-color: bisque;
  border: 2px solid rgba(255, 255, 255, .5);
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 40px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.icon-close {
  fill: #000;
  z-index: 3;
  cursor: pointer;
  padding: 5px;
  position: absolute;
  top: 20px;
  right: 15px;
}

.icon-close use {
  z-index: 2;
}

.modal-wparrep {
  width: 100%;
  height: 500px;
  justify-content: space-between;
  list-style: none;
  display: flex;
}

.modal-left {
  min-width: 50%;
}

.modal-title {
  text-align: center;
  color: #000;
  z-index: 2;
  margin-bottom: 30px;
  font-size: 25px;
}

.modal-quote {
  text-align: left;
  font-size: 28px;
}

.modal-author {
  text-align: right;
  font-size: 20px;
  display: block;
}

.modal-postscript {
  margin-top: 130px;
  font-size: 18px;
  font-style: italic;
}

.modal-right {
  min-width: 50%;
}

.form-details {
  flex-direction: column;
  display: flex;
}

.details-wrapper {
  gap: 10px;
  display: flex;
}

.field {
  height: 45px;
  width: 100%;
  box-shadow: inset 0 0 0 1.5px #d0d0df, rgba(32, 32, 72, .12);
  background: #f4f5f9;
  border: 1px solid #d0d0df;
  border-radius: 6px;
  margin-top: 16px;
  margin-bottom: 2px;
  padding: 2px 10px;
  font-size: 15px;
  display: inline-block;
}

.details-wrapper {
  justify-content: space-between;
  margin-bottom: 20px;
  display: flex;
}

.details-wrapper .field {
  width: 50px;
}

.field::-ms-input-placeholder {
  color: #c9c9d9;
}

.field::placeholder {
  color: #c9c9d9;
}

.button-credit {
  margin: 0 auto;
}

